.m-skip-navigation {
    position: absolute;
    left: 0;
    top: 0;

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    li {
        padding: 0;
        margin: 0;
    }
}

.m-skip-navigation__link {
    display: inline-block;
    width: 300px;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    background-color: var(--global--color-secondary);
    border-radius: 2px;
    padding: 4px 10px;

    &:focus {
        opacity: 1;
        pointer-events: all;
    }
}
